import { useRef } from "react";
import './form.css'




function Form() {
    const emailPtichka = useRef();
    const emailX = useRef();
    const passwordPtichka = useRef();
    const passwordX = useRef();
    const email = useRef()
    const password = useRef()
    const text  = useRef()
    return (
        <>
         <form className="form">
             <h1 className="title">Form</h1>
             <p ref={text} className="text">Enter your password and email</p>
         <input ref={email} className="input-email" type="email" placeholder="email" onChange={(e) => {
             if(e.target.value.length == 0){
                passwordX.current.style.display = 'none'
            }else if(e.target.value.length == 1 || e.target.value.length < 3) {
                passwordX.current.style.display = 'block'
                emailPtichka.current.style.display = 'none'
            }else{
                emailPtichka.current.style.display = 'block'
                passwordX.current.style.display = 'none'
            }
         }}/>
         <h2 ref={passwordPtichka} className="email-ptichka">✔</h2>
         <h2 ref={emailX} className="email-x">✖</h2>
         <input ref={password} className="input-password" type="password" placeholder="password" onChange={(e) => {
             if(e.target.value.length == 0){
                emailX.current.style.display = 'none'
            }else if(e.target.value.length == 1 || e.target.value.length < 8) {
                emailX.current.style.display = 'block'
                passwordPtichka.current.style.display = 'none'
            }else{
                passwordPtichka.current.style.display = 'block'
                emailX.current.style.display = 'none'
            }
         }}/>
         <h2 ref={emailPtichka} className="password-ptichka">✔</h2>
         <h2 ref={passwordX} className="password-x">✖</h2>
         <button className="btn" onClick={(e) => {
             e.preventDefault()
             if(email.current.value == 0 && password.current.value == 0){
              text.current.style.display = 'block'
             }
         }}>Submit</button>
         </form>
        </>
    )
}

export default Form;